<template>
    <div class="btn" @click="changeRoute">
        <img src="../../static/imgs/back.png" />
    </div>
</template>
<script>
export default {
    props: {
        path: {
            type: String,
            default: ''
        }
    },
    methods: {
        changeRoute () {
            if (this.path === '') {
                this.$router.go(-1)
            } else {
                this.$router.push(this.path)
            }
        }
    }
}
</script>
<style lang="scss" scoped>
.btn {
    cursor: pointer;
    position: fixed;
    top: 3rem;
    left: 3rem;
    width: 3rem;
    height: 3rem;
    border-radius: 1.5rem;
    background-color: #fff;
    > img {
        margin-top: 0.8rem;
        margin-left: 0.5rem;
        width: 1.5rem;
        height: 1.5rem;
    }
}
</style>


