import Vue from 'vue'
import Vuex from '../vuex'

Vue.use(Vuex)

export default new Vuex.Store({
    //数据
    state: {
        num:1,
        text:"",
        homeScroll: 0
    },
    getters: {
        getNum: function(state){
            return state.num
        },
        getHomeScroll: (state)=> state.homeScroll
    },
    mutations: {   //突变   同步
        
        addOne(state,payload) {
           
            state.num += payload;
        },

        change(state,text) {
            state.text = text;
        },
        SET_HOME_SCROLL(state,scrollPosition){
            state.homeScroll = scrollPosition
        }
    },
    actions: {   //异步调用
        addOneAsync({commit,dispatch},payload){
            setTimeout(()=>{
               // console.log("suhahda")
              
                commit("addOne",payload)
            },1000)
        }
    },
    modules: {

    }
})


